//we need clearence for more products







import React, { useEffect,useState } from 'react';
import {Link} from 'react-router-dom'
import Card from './Card';


const Cart =() =>{
    
const [total,setTotal]=useState(0)

useEffect(()=>{
    
  setTotal(JSON.parse(sessionStorage.getItem('cart')).reduce((total, item)=>total+item.price,0))

})







        return (
            <div><br/><br/>
<center><h3>total ammount-{total}$</h3></center>

                {sessionStorage.getItem('cart')?<>{JSON.parse(sessionStorage.getItem('cart')).map(res=><>



                    
<Card 
  img={`${res.img}`}
  _id={`${res._id}`}
    pre={`${res.pre}`}
    price={`${res.price}`}
    del="true"

/>



           </>  )
                 
             }</>:<h3>empty cart?!</h3>}

<br></br><br></br>
<center><Link id='btn-3' to='/Checkout'>&nbsp;&nbsp; &nbsp;&nbsp; &nbsp;&nbsp; &nbsp;&nbsp;check out&nbsp;&nbsp; &nbsp;&nbsp; &nbsp;&nbsp; &nbsp;&nbsp;</Link></center>
<br></br><br></br><br></br><br></br>
            </div>
        );
    
}

export default Cart;